.bookingModuleMainWrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: var(--Blanco);
  gap: 20px;
}

.titleBookingWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 70%;
}

#bookingTitle {
  margin-top: 10px;
  margin-bottom: 20px;
}

#bookingImg {
  height: auto;
  width: 100%;
  border-style: hidden;
  border-radius: 15px;
}

.conditionalWrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: auto;
  width: auto;
  gap: 20px;
}

.bookingWrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: auto;
  width: 100%;
  padding: var(--Padding);
}

.pickersWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 50%;
}

.datePickerContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 150px;
  width: 50%;
  text-align: center;
  padding: 10px;
}

.datePickerContainer p {
  height: 60px;
  width: 100%;
}

.datePickerContainer .datePicker {
  width: 100%;
  height: 45px;
  border-radius: 15px;
  padding-left: 10px;
  padding-right: 10px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--Blanco);
}

.react-datepicker {
  border-radius: 15px;
  background-color: var(--Blanco);
}

.react-datepicker__header {
  border-radius: 15px;
  border-top-right-radius: 15px !important;
  background-color: var(--AzulBase);
  border: 1px solid var(--CafeOscuro);
}

.react-datepicker__current-month,
.react-datepicker__day-name {
  color: var(--Blanco);
}

.react-datepicker__day--selected {
  background-color: var(--AzulBase);
}

.timePickerContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 150px;
  width: 50%;
  text-align: center;
  padding: 10px;
}

.timePickerContainer p {
  height: 60px;
  width: 100%;
  text-align: center;
}

.timeSelector {
  width: 100%;
  height: 45px !important;
  border-radius: 15px;
  padding-left: 10px;
  padding-right: 10px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--Blanco);
}

.optionsWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 50%;
}

.peopleContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 150px;
  width: 50%;
  padding: 10px;
}

.peopleContainer #titlePeople {
  height: 60px;
  width: 100%;
  text-align: center;
}

.quantityControlsContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  height: 45px;
  width: 100%;
  margin-top: 35px;
}

.quantityIndicator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 33%;
  background-color: var(--Blanco);
  color: var(--Negro);
  border-style: solid;
  border-width: 1px;
  border-color: var(--Blanco);
}

.quantityMinus {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 33%;
  background-color: var(--Blanco);
  color: var(--Negro);
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-right: 1px hidden;
  border-left: 1px solid var(--Blanco);
  border-top: 1px solid var(--Blanco);
  border-bottom: 1px solid var(--Blanco);
}

.quantityPlus {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 33%;
  background-color: var(--Blanco);
  color: var(--Negro);
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-left: 1px hidden;
  border-right: 1px solid var(--Blanco);
  border-top: 1px solid var(--Blanco);
  border-bottom: 1px solid var(--Blanco);
}

.zonesContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 150px;
  width: 50%;
  padding: 10px;
}

.zonesContainer #titleZones {
  height: 60px;
  width: 100%;
  text-align: center;
}

.zoneSelector {
  width: 100%;
  height: 45px;
  border-radius: 15px;
  padding-left: 10px;
  padding-right: 10px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--Blanco);
  margin-top: 10px;
}

.userWrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: auto;
  width: 90%;
  padding: var(--Padding);
}

.userInputsContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: auto;
  width: 33%;
}

#titleContact {
  height: 60px;
  width: 100%;
  text-align: center;
}

.userInputsContainer input {
  width: 100%;
  height: 40px;
  border-radius: 15px;
  padding-left: 10px;
  padding-right: 10px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--Blanco);
  margin-top: 10px;
}

.userMessaggeContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 33%;
}

#titleMessagge {
  height: 60px;
  width: 100%;
  text-align: center;
}

#userMessaggeInput {
  height: 150px;
  width: 90%;
  border-radius: 15px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--Blanco);
  resize: none;
}

.btnReservarContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 33%;
}

#btnReservar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70px;
  width: 85%;
  border-radius: 15px;
  padding: 10px;
  border-style: solid;
  border-width: 3px;
  border-color: var(--CafeOscuro);
  background-color: var(--CremaBase);
}

#btnReservar:enabled:hover {
  background-color: var(--CafeOscuro);
  color: var(--CremaBase);
  border-color: var(--CremaBase);
}

.calendarIcon {
  width: auto;
  height: 25px;
}

#notificationBooking {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}
